<template>
    <div class="boardWrap">
        <h2>&nbsp;&nbsp;공지 게시판</h2><hr>
        <br>
        <div class="contentWrap">
            <table class="table">
                <thead>
                    <tr>
                        <th style="width: 10%;">번호</th>
                        <th>제목</th>
                        <th style="width: 20%;">작성자</th>
                        <th style="width: 20%;">작성일</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>1</td>
                        <td>제목</td>
                        <td>admin</td>
                        <td>22.22.22</td>
                    </tr>
                </tbody>
            </table>
            <div class="pageWrap">
                <button class="btn btn-dark paging">&lt;</button>
                <button class="btn btn-dark paging">1</button>
                <button class="btn btn-dark paging">&gt;</button>
            </div>
        </div>
    </div>

</template>

<script>
export default {
  data () {
    return {
      name: 'foo'
    }
  },
  mounted () {
    this.load()
  },
  methods: {
    load () {
      this.$axios.get('http://localhost:8080/board/notice/getNoticeList').then(res => {
        console.log(res.data)
      })
    }
  }
}
</script>

<style>
    table{
        text-align: center;
        width: 100%;

    }
    .boardWrap{
        width: 1200px;
        margin: auto;
    }
    .contentWrap{
        display: flex;
        flex-direction: column;
    }
    .pageWrap{
        margin: auto;
        margin-top:30px;
    }
    .crateWrap{
        width: 80px;
        height: 30px;
        margin-left: 90%;

    }
    .crateWrap button{
        width: 100%;
        height: 100%;
    }
</style>



